<template>
  <div v-theme:colum="'wide'" id="show-blogs">
      <h1>博客文章</h1>
      <input type="text" placeholder="搜索" v-model="search">
      <div v-for="blog in filteredBlogs" class="single-blog">
          <router-link v-bind:to="'/blog/' + blog.id">
            <h2 v-rainbow> {{blog.title | to-uppercase}} </h2>
          </router-link>
          <article>
              {{ blog.content | snippet}}
          </article>
      </div>
  </div>
</template>

<script>

export default {
  name: 'show-blogs',
  data(){
      return{
          blogs:[],
          search:""
      }
  },
  created(){

      this.$http.get('https://my-blog-0001.firebaseio.com/posts.json')
        .then( data => {
            // console.log(data);
            // console.log(typeof(data));
            return data.json();
            //只要10个对象
            //this.blogs = data.body.splice(0,10);
            //console.log(this.blogs);
        })
        .then( data=>{
            var blogsArry = [];
            for( let key in data ){
                // console.log(key);
                // console.log(data[key]);
                data[key].id = key;
                blogsArry.push(data[key]);
            }
            // console.log(blogsArry);
            this.blogs = blogsArry;
            // console.log(this.blogs);
        })
  },
  computed:{
      filteredBlogs(){
          return this.blogs.filter( (blog) => {
              return blog.title.match(this.search);
          })
      }
  },
  filters:{
      toUppercase(value){
        //   return value.toUpperCase();
      },
      snippet(value){
        //   只显示0-100的字符
          return value.slice(0,100) + "...";
      }
  },
  directives:{
      'rainbow':{
          bind(el,binding,vnode){
              el.style.color = "#" + Math.random().toString(16).slice(2,8);
          }
      }
  }
}
</script>

<style>
#show-blogs{
	max-width: 800px;
	margin: 0 auto;
    margin-top: 30px;
}

.single-blog{
	padding: 20px;
	margin: 20px 0;
	box-sizing: border-box;
	background: #eee;
    border: 1px dotted #aaa;
}

#show-blogs a{
  color: #444;
  text-decoration: none;
}

input[type="text"]{
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>



