<template>
    <nav>
        <ul>
            <li>
                <router-link to="/" exact>博客</router-link>
                <router-link to="/add" exact>写博客</router-link>
            </li>
        </ul>
    </nav>
</template>

<script>
    export default{
        name:"blog-header"
    }
</script>

<style>
ul{
  list-style-type: none;
  text-align: center;
  margin:0;
}
li{
  display: inline-block;
  margin: 0 10px;
}
/* 虽然没有写a标签，但是<router-link></router-link>
会在浏览器中被当做a 标签处理 */
a{
    color: #fff;
    text-decoration: none;
    padding:12px;
    border-radius: 5px;
}
nav{
    background: crimson;
    padding: 30px 0;
    margin-top: 40px;
}
.router-link-active{
    background: rgba(255,255,255,0.8);
    color: #444;
}
</style>



